function onDOMLoaded() {
    setTheme()
}

function setTheme() {
    try {
        if (localStorage.theme === 'dark' || (!('theme' in localStorage) && window.matchMedia('(prefers-color-scheme: dark)').matches)) {
            document.documentElement.classList.add('dark')
            document.querySelector('meta[name="theme-color"]').setAttribute('content', '#COLOR-DARK')
        } else {
            document.documentElement.classList.remove('dark')
        }
    } catch (_) { }
}

function scrollFunction() {
  if (document.body.scrollTop > 64 || document.documentElement.scrollTop > 64) {
    document.documentElement.classList.add('mini-header')
  } else {
    document.documentElement.classList.remove('mini-header')
  }
} 

document.addEventListener('DOMContentLoaded', onDOMLoaded)
window.addEventListener('scroll', (event) => {scrollFunction()})